.websites-container {
  display: flex;
  justify-content: space-around;
  width: 65%;
  margin: 0 auto;
}
.name {
  text-align: center;
}
.website {
  width: 35%;
  border-radius: 7px;
  display: block;
  margin: 15px auto;
  padding: 0 15px;
  transition: 0.25s;
  color: var(--font-color);
}
.website-info {
  width: 100%;
  border-radius: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px 0;
  padding: 10px;
  transition: 0.25s;
  color: var(--font-color);
  border: 2px solid var(--font-color);
  box-shadow: 0 0 10px 0 var(--font-color);
}
.website .website-image {
  cursor: pointer;
  border: 2px solid var(--font-color);
  box-shadow: 0 0 10px 0 var(--font-color);
  display: block;
  border-radius: 7px;
  margin: 15px auto;
  width: 100%;
}
.website .website-title {
  text-align: left;
  font-weight: bold;
  margin: 0;
  font-size: 16px;
  padding: 0;
  width: 75%;
}
.website .website-links {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 0;
  padding: 0;
}
.website .website-links .website-code-link,
.website .website-links .website-demo-link {
  margin: 5px;
  color: var(--font-color);
  cursor: pointer;
}
.website .website-links .website-code-link {
  font-size: 18px;
}

@media screen and (max-width: 1475px) {
  .website {
    padding: 8px;
  }
  .website .website-title {
    font-size: 14px;
  }
}
@media screen and (max-width: 1300px) {
  .websites-container {
    width: 75%;
  }
}
@media screen and (max-width: 1125px) {
  .websites-container {
    width: 85%;
  }
}
@media screen and (max-width: 975px) {
  .websites-container {
    display: block;
    margin: 0 auto;
  }
  .website {
    margin: 20px auto;
    width: 75%;
  }
  .website .website-title {
    padding-left: 0;
    width: 85%;
  }
  .website .website-links {
    width: 15%;
  }
}
@media screen and (max-width: 768px) {
  .websites-container {
    width: 95%;
  }
  .website {
    margin: 20px auto;
    width: 95%;
  }
}
