.statue-image {
  width: 20%;
  margin: 0 auto 10px;
  display: block;
  border-radius: 0;
}
.footer-info {
  display: block;
  margin: 0 auto;
  width: 75%;
}
.footer-info a,
.footer-info p {
  color: var(--font-color);
  text-decoration: none;
  text-align: center;
  margin: 10px auto;
  white-space: nowrap;
}
.footer-info a {
  margin: 0 10px;
}
.footer-info a:hover {
  text-decoration: underline;
}
@media screen and (max-width: 768px) {
  .statue-image {
    width: 80%;
  }
  .footer-info {
    padding: 20px;
  }
  .footer-info a,
  .footer-info p {
    white-space: normal;
  }
}
