.tutorial {
  position: relative;
  display: inline-block;
  width: 25%;
  margin: 50px;
  padding: 0;
  background-color: #000;
  box-sizing: border-box;
  cursor: pointer;
  border: 0;
}
.tutorial img {
  width: 100%;
  transition: all 0.5s ease-in-out;
  border: 0;

  display: block;
  box-shadow: 0 19px 38px var(--third-color), 0 15px 12px var(--fourth-color);
}
.thumbnail {
  /* border: 1px solid #000; */
  display: block;
  transition: all 0.5s ease-in-out;
}
.thumbnail:hover + .youtube-icon-tb {
  display: block;
  transition: 0.5s all;
}
.thumbnail:hover {
  background-color: #000;
  opacity: 0.15;
  cursor: pointer;
}
.youtube-icon-tb {
  pointer-events: none;
  position: absolute;
  font-size: 60px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}
@media screen and (max-width: 1100px) {
  .tutorial {
    display: block;
    width: 50%;
    margin: 40px auto;
  }
}
@media screen and (max-width: 768px) {
  .tutorial {
    width: 90%;
  }
}
