* {
  font-family: "Avenir", sans-serif;
  box-sizing: border-box;
  -webkit-user-select: none;
  user-select: none;
}
body,
html {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
.smooth-effect {
  transition: all 0.5s ease-in-out;
}
body {
  background-color: var(--bg-color);
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
}
.primary-text {
  color: var(--font-color);
}
.hr-50,
.hr-25 {
  display: block;
  margin: 20px auto;
  border: 2px solid var(--font-color);
  box-shadow: 0 0 10px 0 var(--font-color);
}
.hr-50 {
  width: 50%;
}
.hr-25 {
  width: 25%;
}
.hr-50-left {
  width: 75%;
  margin: 10px 0 30px 0;
  border: 2px solid var(--font-color);
  box-shadow: 0 0 10px 0 var(--font-color);
}
.section {
  margin: 0 auto;
  text-align: center;
  display: block;
  margin-top: 100px;
}
.website-link {
  text-decoration: none;
}
.name {
  color: var(--font-color);
}
@media screen and (max-width: 768px) {
  .hr-50 {
    width: 75%;
  }
  .hr-25 {
    width: 50%;
  }
}
