/* light blue = #a7d8fe */
/* dark blue =  #043458 */
/* lighter dark blue = #5a698b */
:root {
  --bg-color: #fff;
  --theme-color: #dff1ff;
  --font-color: #000;
  --font-size: 16px;
  --secondary-color: #555;
  --third-color: rgba(0, 0, 0, 0.5);
  --fourth-color: rgba(0, 0, 0, 0.22);
}
[data-theme="light"] {
  --bg-color: #cbe8ff;
  --theme-color: #fff;
  --font-color: #000;
  --font-size: 16px;
  --secondary-color: #555;
  --third-color: rgba(0, 0, 0, 0.5);
  --fourth-color: rgba(0, 0, 0, 0.22);
}

[data-theme="dark"] {
  --bg-color: #043458;
  --theme-color: #000;
  --font-color: #fff;
  --font-size: 16px;
  --secondary-color: #eee;
  --third-color: rgba(255, 255, 255, 0.5);
  --fourth-color: rgba(255, 255, 255, 0.22);
}
