.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  box-shadow: 0 5px 15px 0 var(--font-color);
  overflow-y: hidden;
  box-sizing: border-box;
  width: 100%;
  position: absolute;
  z-index: 1;
  top: 0;
  height: 60px;
  background-color: var(--bg-color);
  z-index: 3;
}
.navbar .leftIcon,
.navbar .middleIcon {
  background: none;
  border: none;
  outline: none;
}
.navbar .leftIcon button,
.navbar .middleIcon button {
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  font-size: 20px;
}
.navbar .middleIcon button {
  margin-right: 10px;
}
.navbar .leftIcon button {
  margin-left: 10px;
}
.sidebarToggle {
  cursor: pointer;
  height: 15px;
  width: 15px;
}

.sidebarToggle span {
  content: "";
  width: 20px;
  cursor: pointer;
  height: 3px;
  position: absolute;
  top: 50%;
  right: 20px;
  background: var(--font-color);
  transition: all 250ms ease-out;
  will-change: transform;
}

#sidebar-toggle span:first-child {
  transform: translateY(3px);
  height: 3px;
}
#sidebar-toggle span:last-child {
  transform: translateY(-3px);
  height: 3px;
}

#sidebar-toggle.open span:first-child {
  transform: translateY(0) rotate(45deg);
}

#sidebar-toggle.open span:last-child {
  transform: translateY(0) rotate(-45deg);
}

.sidebar {
  width: 100vw;
  box-shadow: 0 5px 15px 0 var(--font-color);
  height: 100vh;
  background-color: var(--bg-color);
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  transition: right 0.3s;
  transition: 5s all;
  z-index: 2;
}
@keyframes sidebar-opening {
  0% {
    left: 100%;
  }
  50% {
    left: 87.5%;
  }
  100% {
    left: 75%;
  }
}
.sidebar.open {
  display: none;
  overflow: hidden;
  left: 85%;
  animation: 0.25s sidebar-opening linear;
}
@media screen and (max-width: 900px) {
  .sidebar.open {
    left: 65%;
  }
}
