.landing-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}
.landing-content {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 75%;
}
.landing-title {
  flex: 1;
}
.landing-title h1 {
  font-size: 3rem;
  word-wrap: nowrap;
  letter-spacing: 2px;
  margin-bottom: 20px;
  color: var(--font-color);
}
.landing-title h2 {
  font-size: 1rem;
  color: var(--secondary-color);
  font-weight: normal;
  margin-bottom: 20px;
}
.landing-title ul {
  display: block;
  padding: 0;
}
.landing-title li {
  font-size: 3rem;
  margin-right: 20px;
  list-style-type: none;
  display: inline-block;
}
.landing-title li a {
  color: var(--font-color);
}
.landing-image img {
  z-index: 1;
  border: 0;
  width: 500px;
  height: 500px;
  padding: 20px;
  text-align: center;
  -webkit-animation: fly 5s linear infinite;
  -moz-animation: fly 5s linear infinite;
  animation: fly 5s linear infinite;
  transition: all 5s linear;
}
@keyframes fly {
  0% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }
  50% {
    -webkit-transform: rotate(15deg);
    transform: rotate(15deg);
  }

  100% {
    -webkit-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }
}
@media screen and (max-width: 1400px) {
  .landing-title h1 {
    font-size: 2.5em;
  }
  .landing-image img {
    width: 400px;
    height: 400px;
  }
}
@media screen and (max-width: 1050px) {
  .landing-title h1 {
    font-size: 2em;
  }
  .landing-image img {
    width: 350px;
    height: 350px;
  }
}
@media screen and (max-width: 925px) {
  .landing-content {
    max-width: 85%;
    flex-direction: column-reverse;
  }
}
@media screen and (max-width: 500px) {
  .landing-title h1 {
    font-size: 2em;
  }
}
.hr-50 {
  width: 50%;
}
.hr-25 {
  width: 25%;
}
